import React from 'react';

import {
  Card, CardMedia, CardContent,
  Typography,
  Chip,
} from '@material-ui/core';

import './Card.css';



class ProjectCard extends React.PureComponent {
  constructor(props) {
    super(props);
    this.data = this.props.data;
  }

  renderTags() {
    return (this.data.tags.map(tag => 
      <Chip key={tag} label={tag}/>
    ));
  }

  render() {
    return (
      <Card>
	<CardMedia image={this.data.image}/>
	<CardContent>
	  <Typography
	    className='card-title'
	    variant='title'
	  >
	    {this.data.title}
	  </Typography>
	  <p>{this.data.description}</p>
	  {this.renderTags()}
	</CardContent>
      </Card>
    );
  }
}

export default ProjectCard;



